import React from "react";
import Footer from "../components/admin/Footer";
import Navigation from "../components/admin/Navigation";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import BLAllowBranches from "../components/admin/sub/BLAllowBranches";
import BLDisallowBranches from "../components/admin/sub/BLDisallowBranches";
import PLAllowbranches from "../components/admin/sub/PLAllowBranches";
import PLDisallowbranches from "../components/admin/sub/PLDisallowBranches";
import BLPrincipalOrCharges from "../components/admin/sub/BLPrincipalOrCharges";
import PLPrincipalOrCharges from "../components/admin/sub/PLPrincipalOrCharges";
import BLAllowBranchesTable from "../components/admin/sub/BLAllowBranchesTable";
import BLDisallowBranchesTable from "../components/admin/sub/BLDisallowBranchesTable";
import PLAllowBranchesTable from "../components/admin/sub/PLAllowBranchesTable";
import PLDisallowBranchesTable from "../components/admin/sub/PLDisallowBranchesTable";
import BLCancellation from "../components/admin/sub/BLCancellation";
import BLAdjustment from "../components/admin/sub/BLAdjustment";
import PLCancellation from "../components/admin/sub/PLCancellation";
import PLAdjustment from "../components/admin/sub/PLAdjustment";
import NewLoanReport from "../components/admin/sub/Report/businessloan/NewLoanReport";
import BadDebts from "../components/admin/sub/Report/businessloan/BadDebtsReport";
import DSTandInsuranceReport from "../components/admin/sub/Report/businessloan/DSTandInsuranceReport";
import IncentiveReport from "../components/admin/sub/Report/businessloan/IncentiveReport";
import LoanRunningReceivable from "../components/admin/sub/Report/businessloan/LoanRunningReceivable";
import PaidLoanReport from "../components/admin/sub/Report/businessloan/PaidLoanReport";
import VATReport from "../components/admin/sub/Report/businessloan/VATReport";
import PNewLoanReport from  "../components/admin/sub/Report/pensionersloan/NewLoanReport";
import PPaidLoanReport from "../components/admin/sub/Report/pensionersloan/PaidLoanReport";
import PBadDebts from "../components/admin/sub/Report/pensionersloan/BadDebtsReport";
import PIncentiveReport from "../components/admin/sub/Report/pensionersloan/IncentiveReport";
import PLoanRunningReceivable from "../components/admin/sub/Report/pensionersloan/LoanRunningReceivable";
import PVATReport from "../components/admin/sub/Report/pensionersloan/VATReport";
import PDSTandInsuranceReport from "../components/admin/sub/Report/pensionersloan/DSTandInsuranceReport";
import IncomeReport from "../components/admin/sub/Report/pensionersloan/IncomeReport";
import BLZoneTransactions from "../components/admin/sub/Rezoning/businessloan/ZoneTransactions";
import BLRegionTransactions from "../components/admin/sub/Rezoning/businessloan/RegionTransactions";
import BLAreaTransactions from "../components/admin/sub/Rezoning/businessloan/AreaTransactions";
import BLBranchTransactions from "../components/admin/sub/Rezoning/businessloan/BranchTransactions";
import BLRegionRezoning from "../components/admin/sub/Rezoning/businessloan/RegionRezoning";
import BLAreaRezoning from "../components/admin/sub/Rezoning/businessloan/AreaRezoning";
import BLBranchRezoning from "../components/admin/sub/Rezoning/businessloan/BranchRezoning";
import PLZoneTransactions from "../components/admin/sub/Rezoning/pensionersloan/ZoneTransactions";
import PLRegionTransactions from "../components/admin/sub/Rezoning/pensionersloan/RegionTransactions";
import PLAreaTransactions from "../components/admin/sub/Rezoning/pensionersloan/AreaTransactions";
import PLBranchTransactions from "../components/admin/sub/Rezoning/pensionersloan/BranchTransactions";
import PLRegionRezoning from "../components/admin/sub/Rezoning/pensionersloan/RegionRezoning";
import PLAreaRezoning from "../components/admin/sub/Rezoning/pensionersloan/AreaRezoning";
import PLBranchRezoning from "../components/admin/sub/Rezoning/pensionersloan/BranchRezoning";
function AdminRoute() {
  return (
    <div>
      <Router>
        <Navigation />
        <Switch>
          {/* Allow Branches */}
          <Route
            path="/businessloan/allowbranches"
            exact
            component={BLAllowBranches}
          />
          <Route
            path="/businessloan/disallowbranches"
            exact
            component={BLDisallowBranches}
          />
          <Route
            path="/pensionersloan/allowbranches"
            exact
            component={PLAllowbranches}
          />
          <Route
            path="/pensionersloan/disallowbranches"
            exact
            component={PLDisallowbranches}
          />
          {/* Charges Settings */}
          <Route
            path="/businessloan/principalorcharges"
            exact
            component={BLPrincipalOrCharges}
          />
          <Route
            path="/pensionersloan/principalorcharges"
            exact
            component={PLPrincipalOrCharges}
          />
          {/* Allowed Branches */}
          <Route
            path="/businessloan/allowedbranches"
            exact
            component={BLAllowBranchesTable}
          />
          <Route
            path="/businessloan/disallowedbranches"
            exact
            component={BLDisallowBranchesTable}
          />
          <Route
            path="/pensioners/allowedbranches"
            exact
            component={PLAllowBranchesTable}
          />
          <Route
            path="/pensioners/disallowedbranches"
            exact
            component={PLDisallowBranchesTable}
          />
          {/* Cancellation */}
          <Route
            path="/businessloan/cancellation"
            exact
            component={BLCancellation}
          />
          <Route
            path="/businessloan/adjustment"
            exact
            component={BLAdjustment}
          />
          <Route
            path="/pensionersloan/cancellation"
            exact
            component={PLCancellation}
          />
          <Route
            path="/pensionersloan/adjustment"
            exact
            component={PLAdjustment}
          />
          {/* Reports */}
          <Route
            path="/report/businessloan/newloanreport"
            exact
            component={NewLoanReport}
          />
          <Route
            path="/report/businessloan/baddebtsreport"
            exact
            component={BadDebts}
          />
          <Route
            path="/report/businessloan/insurancereport"
            exact
            component={DSTandInsuranceReport}
          />
          <Route
            path="/report/businessloan/incentivereport"
            exact
            component={IncentiveReport}
          />
          <Route
            path="/report/businessloan/loanrunningreceivable"
            exact
            component={LoanRunningReceivable}
          />
          <Route
            path="/report/businessloan/paidloanreport"
            exact
            component={PaidLoanReport}
          />
          <Route
            path="/report/businessloan/vatreport"
            exact
            component={VATReport}
          />
          <Route
            path="/report/pensionersloan/newloanreport"
            exact
            component={PNewLoanReport}
          />
          <Route
            path="/report/pensionersloan/paidloanreport"
            exact
            component={PPaidLoanReport}
          />
          <Route
            path="/report/pensionersloan/baddebtsreport"
            exact
            component={PBadDebts}
          />
          <Route
            path="/report/pensionersloan/incentivereport"
            exact
            component={PIncentiveReport}
          />
          <Route
            path="/report/pensionersloan/loanrunningreceivable"
            exact
            component={PLoanRunningReceivable}
          />
          <Route
            path="/report/pensionersloan/vatreport"
            exact
            component={PVATReport}
          />
          <Route
            path="/report/pensionersloan/insurancereport"
            exact
            component={PDSTandInsuranceReport}
          />
          <Route
            path="/report/pensionersloan/incomereport"
            exact
            component={IncomeReport}
          />

          {/* rezoning */}
          <Route
            path="/rezoning/businessloan/zone-transactions"
            exact
            component={BLZoneTransactions}
          />
          <Route
            path="/rezoning/businessloan/region-transactions"
            exact
            component={BLRegionTransactions}
          />
          <Route
            path="/rezoning/businessloan/area-transactions"
            exact
            component={BLAreaTransactions}
          />
          <Route
            path="/rezoning/businessloan/branch-transactions"
            exact
            component={BLBranchTransactions}
          />
          <Route
            path="/rezoning/businessloan/region-rezoning"
            exact
            component={BLRegionRezoning}
          />
          <Route
            path="/rezoning/businessloan/area-rezoning"
            exact
            component={BLAreaRezoning}
          />
          <Route
            path="/rezoning/businessloan/branch-rezoning"
            exact
            component={BLBranchRezoning}
          />
          <Route
            path="/rezoning/pensioners/zone-transactions"
            exact
            component={PLZoneTransactions}
          />
          <Route
            path="/rezoning/pensioners/region-transactions"
            exact
            component={PLRegionTransactions}
          />
          <Route
            path="/rezoning/pensioners/area-transactions"
            exact
            component={PLAreaTransactions}
          />
          <Route
            path="/rezoning/pensioners/branch-transactions"
            exact
            component={PLBranchTransactions}
          />
          <Route
            path="/rezoning/pensioners/region-rezoning"
            exact
            component={PLRegionRezoning}
          />
          <Route
            path="/rezoning/pensioners/area-rezoning"
            exact
            component={PLAreaRezoning}
          />
          <Route
            path="/rezoning/pensioners/branch-rezoning"
            exact
            component={PLBranchRezoning}
          />
        </Switch>
        <Footer />
      </Router>
    </div>
  );
}

export default AdminRoute;
