import React from "react";
import style from  "./pensionersSub.css";
import BlackLogo from "../image/blacklogo2.png";

function CustomersLedger() {
  return (
    <div>
      <div>
        <div className="card-client" style={style}>
          <div className="container" style={style}>
            <img className="logo" style={style} src={BlackLogo} alt="logo"></img>
          </div>
          <div className="pensioners-form-container" style={style}>
          <div className="input-container" style={style}>
            <label className="label" style={style}>Pensioner's Borrower's Ledger</label>
            <input className="input" style={style} placeholder="First Name" />
            <input className="input" placeholder="Last Name" style={style}/>
            <input className="input" defaultValue="PL" placeholder="PL No." style={style}/>
          </div>
          <div className="btn-containers" style={style}>
            <button className="button-item"style={style}>Close</button>
            <button className="button-item">Generate</button>
          </div>
            </div>         
        </div>
      </div>
    </div>
  );
}

export default CustomersLedger;
