import React, { useState } from "react";
import "../sub/css/Substyles.css";
import BlackLogo from "./image/blacklogo2.png";
import { pathToHome } from "../SidebarData";
import { Link } from "react-router-dom";
function PLAppForm() {
  const [option, setOption] = useState("");

  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };

  return (
    <div>
      <div>
        <div className="card-client">
          <div className="container">
            <img className="ml-logo" src={BlackLogo} alt="logo"></img>
          </div>
          <div className="form-container">
            <div className="label-holder">
              <label className="title-label">Print Form</label>
            </div>
            <div className="input-container-dropdown">
              <select
                className="option-newloan-client"
                value={option}
                onChange={handleChange}
                inputprops={{
                  name: "age",
                  id: "age-native-helper",
                }}
              >
                <option aria-label="None" value="" />
                <option value={"Application Form"}>Application Form</option>
                <option value={"SBL KYC form"}>SBL KYC form</option>
              </select>
            </div>
            <div className="btn-containers">
              {pathToHome.map((item) => {
                return (
                  <Link to={item.path}>
                    <button className="button-item">Close</button>
                  </Link>
                );
              })}
              <button className="button-item">
                <span>Search</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PLAppForm;
