import React, { useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import ReportBusinessLoan from "./ReportBusinessLoan";
import ReportPensionersLoan from "./ReportPensionersLoan";
const SidebarLink = styled(Link)`
  display: flex;
  color: #e1e9fc;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 5px;
  list-style: none;
  height: 30px;
  text-decoration: none;
  font-size: 14px;
  &:hover {
    background: #252831;
    border-left: 4px solid red;
    cursor: pointer;
  }
`;

const SidebarLabel = styled.span`
  margin-left: 30px;
`;

const DropdownLink = styled(Link)`
  background: #252831;
  height: 30px;
  padding-left: 3rem;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 14px;

  &:hover {
    background: grey;
    cursor: pointer;
  }
`;

const SubMenu = ({ item }) => {
  const [subnav, setSubnav] = useState(false);
  const [subcom, setSubCom] = useState(false);
  const showSubnav = () => setSubnav(!subnav);
  const showSubCom = () => setSubCom(!subcom);

  return (
    <>
      <SidebarLink to={item.path} onClick={item.subNav && showSubnav}>
        <div>
          {item.icon}
          <SidebarLabel>{item.title}</SidebarLabel>
        </div>
        <div>
          {item.subNav && subnav
            ? item.iconOpened
            : item.subNav
            ? item.iconClosed
            : null}
        </div>
      </SidebarLink>
      {console.log(item.subNav)}
      {subnav &&
        item.subNav.map((item, index) => {
          return (
            <DropdownLink
              to={item.path}
              key={index}
              onClick={item.subCom && showSubCom}
            >
              {item.icon}
              <SidebarLabel>{item.title}</SidebarLabel>
              {item.subTitle === "Business Loan" ? (
                <ReportBusinessLoan />
              ) : (
                <></>
              )}
              {item.subTitle === "Pensioner Loan" ? (
                <ReportPensionersLoan />
              ) : (
                <></>
              )}
            </DropdownLink>
          );
        })}
    </>
  );
};

export default SubMenu;
