import React from "react";
import "../css/AdminStyles.css";
function BLPrincipalOrCharges() {
  return (
    <>
      <div className="card1">
        <div>
          <div className="label-container">
            <label className="table-title">SBL Settings</label>
          </div>
          <table className="table">
            <tbody>
              <tr>
                <td className="table-label">Terms</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="6" />
                </td>
              </tr>
              <tr>
                <td className="table-label">Lending Limit</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="25, 000.00" />
                </td>
              </tr>
              <tr>
                <td className="table-label">Free</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="0.00" />
                </td>
              </tr>
              <tr>
                <td className="table-label">VAT</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="12" />
                </td>
              </tr>
              <tr>
                <td className="table-label">Penalty</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="2" />
                </td>
              </tr>
              <tr>
                <td className="table-label">Interest</td>
                <td className="table-value">
                  <input className="table-input" defaultValue="4" />
                </td>
              </tr>
            </tbody>
          </table>
          <hr></hr>
          <div>
            <button className="button1">Update</button>
            <button className="button1">Close</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default BLPrincipalOrCharges;
