import React from "react";
import "../css/AdminStyles.css";
import logo from "../image/blacklogo2.png";
function BLDisallowBranches() {
  return (
    <>
      <div className="card-admin">
        <div className="container">
          <img className="logo" src={logo} alt="logo"></img>
        </div>
        <div className="input-container">
          <label className="label">SBL Disallow Branches</label>
          <input className="input" placeholder="Branch Code" />
          <input className="input" placeholder="Zone Code" />
        </div>
        <div className="btn-container">
          <button className="button">Close</button>
          <button className="button">
            <span>Go</span>
          </button>
        </div>
      </div>
    </>
  );
}

export default BLDisallowBranches;
