import React, { useState } from "react";
import style from "./businessSub.css";
import BlackLogo from "../image/blacklogo2.png";
import moment from "moment";

function PLReportMonthly() {
  const [option, setOption] = useState("");
  const month = moment.months();
  const years = Array.from(Array(new Date().getFullYear() - 1980), (_, i) =>
    (i + 1990).toString()
  );
  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };
  return (
    <div>
      <div className="card-client-businessub" style={style}>
        <div className="container" style={style}>
          <img className="business-logo" src={BlackLogo} alt="logo"style={style} ></img>
        </div>
        <div className="sblform-container" style={style}>
          <div className="input-container" style={style}>
            <label className="label" style={style}>SBL Paid Report</label>
            <select
              className="option-report-monthly"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              {month.map((item) => (
                <option value={item}>{item}</option>
              ))}
            </select>
            <select
              className="option-report-monthly"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              {years.map((item) => (
                <option value={item}>{item}</option>
              ))}
            </select>
          </div>
          <div className="btn-containers" style={style}>
            <button className="button-item" style={style}>Close</button>
            <button className="button-item" style={style}>Generate</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PLReportMonthly;
