import React from "react";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import { Link } from "react-router-dom";
import MenuItem from "@material-ui/core/MenuItem";
import { subComponent } from "./SidebarData";
import { AiOutlineCaretRight } from "react-icons/ai";

function ReportBusinessLoan() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const DeepDiv= styled.div`
    font-size: 14px;
  `;
  const SubLink = styled(Link)``;
  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <AiOutlineCaretRight />
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {subComponent.map((item, index) => {
          return (
            <DeepDiv key={index}>
              {item.subNav.map((item, id) => {
                return (
                  <div>
                    <SubLink to={item.path} key={id}>
                      <MenuItem onClick={handleClose}>{item.title}</MenuItem>
                    </SubLink>
                  </div>
                );
              })}
            </DeepDiv>
          );
        })}
      </Menu>
    </div>
  );
}
export default ReportBusinessLoan;
