import React, {useState} from "react";
import "../Styles.css";
function BranchRezoning() {
  const [option, setOption] = useState("");
  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };
  return (
    <>
      <div className="card-rezoning">
        <div className="table-container-incentive">
          <table className="table">
            <tbody>
              <div className="label">
                <label className="table-title">Branch Rezoning</label>
              </div>
              <tr>
                <td className="table-label">Zone</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>     
              <tr>
                <td className="table-label">Region</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>  
              <tr>
                <td className="table-label">Area</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr> 
              <tr>
                <td className="table-label">Branch</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>                
            </tbody>
          </table>

          <table className="table">
            <tbody>
              <tr>
                <td className="table-label">New Zone</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>     
              <tr>
                <td className="table-label">New Region</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>  
              <tr>
                <td className="table-label">New Area</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr> 
              <tr>
                <td className="table-label">New Branch</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>               
            </tbody>
          </table>
          <hr></hr>        
          <div className="btn-container">
            <button className="button2">Close</button>
            <button className="button1">Update</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default BranchRezoning;
