import React from "react";
// import Swal from "sweetalert2";
import "../css/AdminStyles.css";
function PLDisallowBranchesTable() {
  const data = [];
  return (
    <>
      <div className="card-table">
        <div>
          <label className="allowed-label">
            Pensioner's Disallowed Branches
          </label>
          <button className="download-btn">download</button>
        </div>
        <table className="allowed">
          <thead className="thead">
            <tr>
              <th>Branch Code</th>
              <th>Zone Code</th>
              <th>Status</th>
              <th>Branch Name</th>
              <th>Tin Number</th>
              <th>Remarks</th>
            </tr>
          </thead>
          <tbody className="tbody">
            {data.map((item) => (
              <tr>
                <td>{item.branch_code}</td>
                <td>{item.zone_code}</td>
                <td>{item.status}</td>
                <td>{item.branch_name}</td>
                <td>{item.tin_number}</td>
                <td>{item.remarks}</td>
              </tr>
            ))}
            <tr></tr>
          </tbody>
        </table>
      </div>
    </>
  );
}

export default PLDisallowBranchesTable;
