import React from "react";
import style from "./css/Substyles.css";
import BlackLogo from "./image/blacklogo2.png";
import { pathToHome } from "../SidebarData";
import { Link } from "react-router-dom";
function NLNewLoan() {
  return (
    <div>
      <div className="card-client" style={style}>
        <div className="container" style={style}>
          <img
            className="ml-logo"
            style={style}
            src={BlackLogo}
            alt="logo"
          ></img>
        </div>
        <div className="form-container" style={style}>
          <div className="label-holder" style={style}>
            <label className="title-label" style={style}>
              Search Small Business Loan KYC
            </label>
          </div>
          <div className="input-container" style={style}>
            <input className="input" style={style} placeholder="First Name" />
            <input className="input" style={style} placeholder="Last Name" />
          </div>
          <div className="btn-containers" style={style}>
            {pathToHome.map((item) => {
              return (
                <Link to={item.path}>
                  <button className="button-item" style={style}>
                    Close
                  </button>
                </Link>
              );
            })}
            <button className="button-item" style={style}>
              <span>Search</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NLNewLoan;
