import React from "react";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import { Link } from "react-router-dom";
import MenuItem from "@material-ui/core/MenuItem";
import { SubComponent2 } from "./SidebarData";
import { AiOutlineCaretRight } from "react-icons/ai";

function ReportPensionersLoan() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const DeepDiv = styled.div`
    font-size: 14px;
  `;
  const Links = styled(Link)`
    font-size: 14px;
  `;
  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <AiOutlineCaretRight />
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {SubComponent2.map((item, index) => {
          return (
            <DeepDiv key={index}>
              {item.subNav.map((item, index) => {
                return (
                  <div>
                    <Links to={item.path} key={index}>
                      <MenuItem onClick={handleClose}>{item.title}</MenuItem>
                    </Links>
                  </div>
                );
              })}
            </DeepDiv>
          );
        })}
      </Menu>
    </div>
  );
}
export default ReportPensionersLoan;
