import React, { useState } from "react";
import style from  "./pensionersSub.css";
import BlackLogo from "../image/blacklogo2.png";
import moment from "moment";

function PLReportMonthly() {
  const [option, setOption] = useState("");
  const MONTH = moment.months();
  const YEARS = Array.from(Array(new Date().getFullYear() - 1980), (_, i) =>
    (i + 1990).toString()
  );
  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };
  return (
    <div>
      <div className="card-client" style={style}>
        <div className="container" style={style}>
          <img className="logo" src={BlackLogo} alt="logo" style={style}></img>
        </div>
        <div className="pensioners-form-container" style={style}>
          <div className="input-container" style={style}>
            <label className="label" style={style}>Pensioner's Paid Report</label>
            <select
              className="option-report-monthly"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              {MONTH.map((item, index) => (
                <option value={item} key={index}>
                  {item}
                </option>
              ))}
            </select>
            <select
              className="option-report-monthly"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              {YEARS.map((item, index) => (
                <option value={item} key={index}>
                  {item}
                </option>
              ))}
            </select>
          </div>
          <div className="btn-containers">
            <button className="button-item">Close</button>
            <button className="button-item">Generate</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PLReportMonthly;
