import React from "react";
import style from "./businessSub.css";
import BlackLogo from "../image/blacklogo2.png";

function CustomersLedger() {
  return (
    <div>
      <div>
        <div className="card-client-businessub" style={style}>
          <div className="container" style={style}>
            <img className="business-logo" src={BlackLogo} style={style} alt="logo"></img>
          </div>
          <div className="sblform-container" style={style}>
            <div className="input-container" style={style}>
              <label className="label" style={style}>SBL Borrower's Ledger</label>
              <input className="input" placeholder="First Name" style={style}/>
              <input className="input" placeholder="Last Name" style={style}/>
              <input
                className="input"
                style={style}
                defaultValue="SBL"
                placeholder="SBL No."
              />
            </div>
            <div className="btn-containers" style={style}>
              <button className="button-item" style={style}>Close</button>
              <button className="button-item" style={style}>Generate</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CustomersLedger;
