import style from "../App.css";
import React from "react";
import Footer from "../components/client/Footer";
import Image from "../image/BusinessLoan.png";
import Home from "../components/client/Home";
import Sidebar from "../components/client/Sidebar";
import NLAppForm from "../components/client/sub/NLAppForm";
import NLNewLoan from "../components/client/sub/NLNewLoan";
import NLReprint from "../components/client/sub/NLReprint";
import PLAppForm from "../components/client/sub/PLAppForm";
import PLNewLoan from "../components/client/sub/PLNewLoan";
import PLReprint from "../components/client/sub/PLReprint";
import ALBusinessLoan from "../components/client/sub/ALBusinessLoan";
import ALPensionersLoan from "../components/client/sub/ALPensionersLoan";
import PIBusinessLoanNewLoan from "../components/client/sub/PIBusinessLoanNewLoan";
import PIORReprint from "../components/client/sub/PIORReprint";
import PIpensionerPaymentInquiry from "../components/client/sub/PIpensionerPaymentInquiry";
import PIpensionerORReprint from "../components/client/sub/PIpensionerORReprint";
import DLBusinessLoan from "../components/client/sub/DLBusinessLoan";
import DLPensionersLoan from "../components/client/sub/DLPensionersLoan";
import NewLoanReportDaily from "../components/client/sub/businessSub/NLReportDaily";
import NewLoanReportMonthly from "../components/client/sub/businessSub/NLReportMonthly";
import PaidLoanReportDaily from "../components/client/sub/businessSub/PLReportDaily";
import PaidLoanReportMonthly from "../components/client/sub/businessSub/PLReportMonthly";
import CustomersLedger from "../components/client/sub/businessSub/CustomersLedger";
import LoanRunningReceivable from "../components/client/sub/businessSub/LRReceivable";
import NewLoanReportDaily2 from "../components/client/sub/pensionersSub/NLReportDaily";
import NewLoanReportMonthly2 from "../components/client/sub/pensionersSub/NLReportMonthly";
import PaidLoanReportDaily2 from "../components/client/sub/pensionersSub/PLReportDaily";
import PaidLoanReportMonthly2 from "../components/client/sub/pensionersSub/PLReportMonthly";
import CustomersLedger2 from "../components/client/sub/pensionersSub/CustomersLedger";
import LoanRunningReceivable2 from "../components/client/sub/pensionersSub/LRReceivable";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function ClientRoute() {
  return (
    <div>
      <img className="Image" style={style} src={Image} alt="img"></img>
      <Router>
        <Sidebar />
        <Switch>
          <Route path="/" exact component={Home} />

          {/* NewLoan Routes */}
          <Route path="/newloan/applicationform" exact component={NLAppForm} />
          <Route path="/newloan/newloan" exact component={NLNewLoan} />
          <Route path="/newloan/reprint" exact component={NLReprint} />
          <Route
            path="/pensioners/applicationform"
            exact
            component={PLAppForm}
          />
          <Route path="/pensioners/newloan" exact component={PLNewLoan} />
          <Route path="/pensioners/reprint" exact component={PLReprint} />

          {/* Approved Loan Routes */}
          <Route
            path="/approvedloan/businessloan"
            exact
            component={ALBusinessLoan}
          />
          <Route
            path="/approvedloan/pensionersloan"
            exact
            component={ALPensionersLoan}
          />

          {/* Payment Inquiry Routes */}
          <Route
            path="/businessloan/paymentinquiry"
            component={PIBusinessLoanNewLoan}
          />
          <Route path="/businessloan/orreprint" component={PIORReprint} />
          <Route
            path="/pensionerloan/paymentinquiry"
            component={PIpensionerPaymentInquiry}
          />
          <Route
            path="/pensionerloan/orreprint"
            component={PIpensionerORReprint}
          />

          {/* Demand Letter Routes */}
          <Route path="/demandletter/businessloan" component={DLBusinessLoan} />
          <Route
            path="/demandletter/pensionersloan"
            component={DLPensionersLoan}
          />

          {/* Report Routes */}

          <Route
            path="/report/business/newloanreportdaily"
            exact
            component={NewLoanReportDaily}
          />
          <Route
            path="/report/business/newloanreportmonthly"
            exact
            component={NewLoanReportMonthly}
          />
          <Route
            path="/report/business/paidloanreportdaily"
            exact
            component={PaidLoanReportDaily}
          />
          <Route
            path="/report/business/paidlaonreportmonthly"
            exact
            component={PaidLoanReportMonthly}
          />
          <Route
            path="/report/business/customersledger"
            exact
            component={CustomersLedger}
          />
          <Route
            path="/report/business/loanrunningreceivable"
            exact
            component={LoanRunningReceivable}
          />
          <Route
            path="/report/pensioner/newloanreportdaily"
            exact
            component={NewLoanReportDaily2}
          />
          <Route
            path="/report/pensioner/newloanreportmonthly"
            exact
            component={NewLoanReportMonthly2}
          />
          <Route
            path="/report/pensioner/paidloanreportdaily"
            exact
            component={PaidLoanReportDaily2}
          />
          <Route
            path="/report/pensioner/paidlaonreportmonthly"
            exact
            component={PaidLoanReportMonthly2}
          />
          <Route
            path="/report/pensioner/customersledger"
            exact
            component={CustomersLedger2}
          />
          <Route
            path="/report/pensioner/loanrunningreceivable"
            exact
            component={LoanRunningReceivable2}
          />
        </Switch>
        <Footer />
      </Router>
    </div>
  );
}
export default ClientRoute;
