import React from "react";
import "../sub/css/Substyles.css";
import BlackLogo from "./image/blacklogo2.png";
import { pathToHome } from "../SidebarData";
import { Link } from "react-router-dom";
function PLNewLoan() {
  return (
    <div>
      <div>
        <div className="card-client">
          <div className="container">
            <img className="ml-logo" src={BlackLogo} alt="logo"></img>
          </div>
          <div className="form-container">
            <div className="label-holder">
              <label className="title-label">
                Search Small Business Loan KYC
              </label>
            </div>
            <div className="input-container">
              <input className="input" placeholder="First Name" />
              <input className="input" placeholder="Last Name" />
            </div>
            <div className="btn-containers">
              {pathToHome.map((item) => {
                return (
                  <Link to={item.path}>
                    <button className="button-item">Close</button>
                  </Link>
                );
              })}
              <button className="button-item">
                <span>Search</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PLNewLoan;
