import React, { useState } from "react";
import style from  "./pensionersSub.css";
import styled from "styled-components";
import Calendar from "./Calendar";
import BlackLogo from "../image/blacklogo2.png";

function NLAppForm() {
  const [option, setOption] = useState("");
  const [value, onChange] = useState(new Date());

  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };
  const Container = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  `;
  return (
    <div>
      <div className="card-client" style={style}>
        <div className="container" style={style}>
          <img className="logo" src={BlackLogo} alt="logo" style={style}></img>
        </div>
        <div className="pensioners-form-container" style={style}>
          <div className="input-container" style={style}>
            <label className="label" style={style}>Pensioner's Daily New Loan Report</label>
            <select
              className="option-report-daily"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              <option value={"Application Form"}>Branch</option>
              <option value={"SBL KYC form"}>Employee</option>
            </select>
            <Container>
              <Calendar onChange={onChange} value={value} />
            </Container>
          </div>
          <div className="btn-containers" style={style}>
            <button className="button-item" style={style}>Close</button>
            <button className="button-item" style={style}>Generate</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NLAppForm;
