import React, { useState, useEffect } from "react";
import style from "./css/Substyles.css";
import BlackLogo from "./image/blacklogo2.png";
import axios from "axios";
import pdf from "../../../file/SBL_Application_Form.pdf";
import { pathToHome } from "../SidebarData";
import { Link } from "react-router-dom";
// import { saveAs } from "file-saver";
function NLAppForm() {
  const [option, setOption] = useState("");

  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };

  // call get api
  useEffect(() => {
    getApi();
  }, []);
  const getApi = async () => {
    const data = await axios.get("/api/data");
    return data;
  };
  // const printNewloan = async () => {
  //   const data = await getNewloanPdf();
  //   console.log(data, 'dataaaaaaaaaaaaaaaaaaaa');
  //   const blob = new Blob([data], { type: "application/pdf" });
  //   console.log(blob, "blooooobbbbbbbbbbb");
  //   saveAs(blob, `SBL_Application_Form.pdf`);
  // };

  // const getNewloanPdf = async () => {
  //   const getPdf =
  //     (await axios.get(`/api/data/download/SBL_Application_Form`),
  //     {
  //       headers: {
  //         "Content-Type": "multiplepart/form-data",
  //       },
  //       responseType: "arraybuffer",
  //     });
  //   console.log(getPdf, "pdffffffffffffffffffffffff");
  //   return getPdf;
  // };

  return (
    <div>
      <div className="card-client" id="card-client" style={style}>
        <div className="container" style={style}>
          <img
            className="ml-logo"
            src={BlackLogo}
            alt="logo"
            style={style}
          ></img>
        </div>
        <div className="form-container" style={style}>
          <div className="label-holder" style={style}>
            <label className="title-label" style={style}>
              Print Form
            </label>
          </div>
          <div className="input-container-dropdown" style={style}>
            <select
              className="option-newloan-client"
              style={style}
              value={option}
              onChange={handleChange}
              inputprops={{
                name: "age",
                id: "age-native-helper",
              }}
            >
              <option aria-label="None" value="" />
              <option value={pdf}>Application Form</option>
              <option value={"SBL KYC form"}>SBL KYC form</option>
            </select>
          </div>
          <div className="btn-containers" style={style}>
            {pathToHome.map((item) => {
              return (
                <Link to={item.path}>
                <button className="button-item" style={style}>
                  Close
                </button>
                </Link>
              );
            })}
            <a href={pdf} download="file.pdf">
              <button className="button-item" style={style} download="file.pdf">
                Print
              </button>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NLAppForm;
