import React from "react";
import styled from "styled-components";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import { Link } from "react-router-dom";
import MenuItem from "@material-ui/core/MenuItem";
import { PLRezoningData } from "../NavigationData";
import { AiOutlineCaretRight } from "react-icons/ai";

function RezoningPensionersLoan() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const SubLink = styled(Link)``;
  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <AiOutlineCaretRight style={{color:"white"}}/>
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {PLRezoningData.map((item, index) => {
          return (
            <SubLink to={item.path} key={index}>
              <MenuItem onClick={handleClose}>{item.title}</MenuItem>
            </SubLink>
          );
        })}
      </Menu>
    </div>
  );
}
export default RezoningPensionersLoan;
