import React, {useState} from "react";
import moment from "moment";
import "../Styles.css";
function IncentiveReport() {
  const [option, setOption] = useState("");
  const MONTH = moment.months();
  const YEARS = Array.from(Array(new Date().getFullYear() - 2014), (_, i) =>
    (i + 2021).toString()
  );
  const handleChange = (e) => {
    const { event } = e.target;
    setOption(event);
  };
  return (
    <>
      <div className="card-report">
        <div className="table-container-incentive">
          <table className="table">
            <tbody>
              <div className="label">
                <label className="table-title">SBL Incentive Report</label>
              </div>
              <tr>
                <td className="table-label">Zone</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="VISMIN">
                      VISMIN
                    </option>
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Region</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Area</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Branch</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Month</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                    {MONTH.map((item, index) => (
                      <option value={item} key={index}>{item}</option>
                    ))}
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Year</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    {YEARS.map((item, index) => (
                      <option value={item} key={index}>{item}</option>
                    ))}
                  </select>
                </td>
              </tr>
              <tr>
                <td className="table-label">Type</td>
                <td className="table-value">
                  <select
                    className="option"
                    value={option}
                    onChange={handleChange}
                  >
                    <option aria-label="None" value="">
                      -Select-
                    </option>
                    <option value="old">Old</option>
                    <option value="new">New</option>
                  </select>
                </td>
              </tr>
            </tbody>
          </table>
          <hr></hr>
          <div className="notice">
            <h3>NOTICE</h3>
            <p>*Type Old - Transactions from version 1.0 to version 2.5</p>
            <p>*Type New - Transaction from version 2.6 Upload</p>
          </div>
          <div className="btn-container">
            <button className="button2">Close</button>
            <button className="button1">Generate</button>
          </div>
        </div>
      </div>
    </>
  );
}

export default IncentiveReport;
